Header {
  background-color: #124449;
  height: auto;
  padding: 20px;
  color: #20dbd8;
  font-family: 'Limelight', cursive;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  align-content: center;
  justify-content: space-between;
}

h1 {
  font-size: 40px;
  font-weight: bold;
}

div {
  font-size: 20px;
  font-weight: bold;
}
a {
  color: #179fa3;
  text-decoration: none;
}

a:hover {
  color: #20dbd8;
}

nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
}

nav a {
  margin: 5px;
}

/* Smaller screens and tablets */
@media only screen and (max-width: 960px) {
  Header {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 0px;
  }
  h1 {
    font-size: 30px;
    margin: 5px;
  }
  nav {
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }
}

